import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import styles from "./styles.scss";
import { actions, actionHandlers } from "./actions";
import { view } from "./view";

/**
 * A component which renders the angular portal experience of Catalog in iframe
 *
 * ```
 * <sn-catalog-iframe table={sc_cat_item|sc_cat_item_guide|sc_cat_item_producer} sysId={sysId} parentTable={parentTable} parentSysId={parentSysId}>
 * </sn-catalog-iframe>
 * ```
 *
 * @seismicElement sn-catalog-iframe
 * @summary A component which renders the angular portal experience of Catalog in iframe
 */

createCustomElement("sn-catalog-iframe", {
	renderer: { type: snabbdom },
	view,
	properties: {
		sysId: {
			reflect: true
		},
		table: {
			reflect: true
		},
		tabId: {
			reflect: true
		},
		parentTable: {
			reflect: true
		},
		parentSysId: {
			reflect: true
		},
		pageId: {
			default: "sc_category",
			reflect: false
		},
		categoryId: {
			reflect: false
		}
	},
	initialState: {
		iFrameOrigin: null,
		iFrameWindow: null
	},
	actions,
	actionHandlers,
	styles
});
