import { actionTypes } from "@servicenow/ui-core";
const { COMPONENT_DISCONNECTED } = actionTypes;
import * as constants from "./constants";
import { t } from "sn-translate";

export const actions = {
	[constants.IFRAME_LOADED]: {
		private: true
	}
};

export const actionHandlers = {
	[constants.IFRAME_LOADED]: ({ state, dispatch, updateState, action }) => {
		let { elm: iFrame } = action.payload;
		let { iFrameOrigin, iFrameWindow } = state;

		if (iFrame && (!iFrameOrigin || !iFrameWindow)) {
			if (!iFrameWindow) {
				iFrameWindow = iFrame.contentWindow;
				if (!iFrameOrigin) {
					iFrameOrigin = iFrameWindow.location.origin;
				}
				iFrameWindow.addEventListener(
					"message",
					onMessageIFrame.bind(null, state.properties, dispatch, iFrameOrigin),
					false
				);
			} else {
				iFrameWindow.removeEventListener("message", onMessageIFrame);
				iFrameWindow.addEventListener(
					"message",
					onMessageIFrame.bind(null, state.properties, dispatch, iFrameOrigin),
					false
				);
			}
			updateState({
				path: "iFrameOrigin",
				value: iFrameOrigin,
				operation: "set",
				shouldRender: false
			});
			updateState({
				path: "iFrameWindow",
				value: iFrameWindow,
				operation: "set",
				shouldRender: false
			});
			dispatch(constants.COMPONENT_SET_TITLE, t("Catalogs"));
		}
	},
	[COMPONENT_DISCONNECTED]: ({ state, updateState }) => {
		let { iFrameWindow } = state;
		iFrameWindow.removeEventListener("message", onMessageIFrame);
		updateState({
			path: "iFrameWindow",
			value: iFrameWindow,
			operation: "set",
			shouldRender: false
		});
	}
};

export const onMessageIFrame = (
	{ tabId, parentSysId, parentTable },
	dispatch,
	iFrameOrigin,
	evt
) => {
	if (evt.origin !== iFrameOrigin) return;

	console.log("CatalogIFrame - onMessageIFrame", evt); /* eslint-disable-line */
	let type = "";
	switch (evt.data.msg) {
		case constants.CATALOG_ITEM_CLOSED:
			type = constants.UPDATE_ONE_TAB_CLOSE;
			dispatch(constants.UPDATE_ONE_TAB, { type, tab_id: tabId });
			dispatch(constants.COMPONENT_CLOSE, {});
			break;
		case constants.CATALOG_ITEM_SET_TITLE:
			type = constants.UPDATE_ONE_TAB_TITLE;
			dispatch(constants.UPDATE_ONE_TAB, {
				type,
				tab_id: tabId,
				title: evt.data.title
			});
			dispatch(constants.COMPONENT_SET_TITLE, { title: evt.data.title });
			break;
		case constants.TARGET_RECORD_SELECTED:
			type = constants.UPDATE_ONE_TAB_CLOSE;
			dispatch(constants.UPDATE_ONE_TAB, { type, tab_id: tabId });
			dispatch(constants.ITEM_SELECTED, {
				parent_table: parentTable,
				parent_sys_id: parentSysId,
				table: evt.data.target_table,
				sys_id: evt.data.target_sys_id,
				params: { query: evt.data.target_query }
			});
			dispatch(constants.COMPONENT_RECORD_DETAILS, {
				parent_table: parentTable,
				parent_sys_id: parentSysId,
				table: evt.data.target_table,
				sysId: evt.data.target_sys_id,
				title: evt.data.target_number,
				query: evt.data.target_query
			});
			break;
		default:
			break;
	}
};
