import * as constants from "./constants";

const getCatalogURL = (table, sysId, pageId, categoryId) => {
	let baseURL = "/swp";

	if (!table || !sysId || sysId == "-1") {
		if (pageId && categoryId)
			return `${baseURL}?id=${pageId}&sys_id=${categoryId}`;
		else return baseURL;
	}
	if (table == "sc_cat_item_producer") table = "sc_cat_item";
	return `${baseURL}?id=${table}&sys_id=${sysId}&sysparm_category=${categoryId}`;
};

export const view = (state, { dispatch }) => {
	let {
		properties: { table, sysId, parentTable, parentSysId, pageId, categoryId }
	} = state;
	const src = getCatalogURL(table, sysId, pageId, categoryId);
	return (
		<div className="iframe">
			<iframe
				src={src}
				on-load={evt => {
					dispatch(constants.IFRAME_LOADED, { elm: evt.target });
				}}
				sandbox="allow-same-origin allow-forms allow-scripts allow-modals allow-popups"
				dataParentSysId={parentSysId}
				dataParentTable={parentTable}
			/>
		</div>
	);
};
